<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>

    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label>
        <input type="text" v-model.number="amount" name="count">
      </label>

      <button type="button" aria-label="Добавить один товар" @click.prevent="incrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }}₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  filters: {
    numberFormat,
  },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return (this.item.amount);
      },
      set(value) {
        this.$store.dispatch('updateCartProduct', { productId: this.item.productId, amount: value })
          .then(() => {
          });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProduct']),

    deleteProduct() {
      this.deleteCartProduct({ productId: this.item.productId })
        .then(() => {

        });
    },
    incrementAmount() {
      this.$store.commit('updateCartProduct', { productId: this.item.productId, amount: this.item.amount + 1 });
    },
    decrementAmount() {
      this.$store.commit('updateCartProduct', { productId: this.item.productId, amount: this.item.amount - 1 });
    },
  },

};
</script>
